import { useRef } from "react"
import { Box, ButtonBase, Typography } from "@rp/react/web/mui/core"
import { Cancel } from "@rp/react/web/mui/icons"

type Props = {
  label?: string
  onClose?: () => void
  onClick?: () => void
  children: React.ReactNode
}

const ChatFilterChip = ({ label, onClose, children, onClick }: Props) => {
  const isMouseDown = useRef(false)
  const hasCursorMoved = useRef(false)

  return (
    <Box
      sx={{
        backgroundColor: "overlay.surface",
        display: "flex",
        alignItems: "center",
        py: 0.5,
        px: 1,
        borderRadius: 1,
        gap: 1,
        cursor: onClick ? "pointer" : undefined,
        userSelect: "none",
      }}
      onMouseDown={() => {
        isMouseDown.current = true
      }}
      onMouseMove={() => {
        if (isMouseDown.current) {
          hasCursorMoved.current = true
        }
      }}
      onMouseUp={() => {
        if (!hasCursorMoved.current) {
          onClick?.()
        }

        hasCursorMoved.current = false
        isMouseDown.current = false
      }}
    >
      {label ? (
        <Typography sx={{ color: "grey.300", whiteSpace: "nowrap" }}>{label}</Typography>
      ) : null}
      {children}
      {onClose ? (
        <ButtonBase
          onClick={event => {
            event.stopPropagation()
            onClose()
          }}
          onMouseUp={event => event.stopPropagation()}
        >
          <Cancel sx={{ height: 20, width: 20 }} />
        </ButtonBase>
      ) : null}
    </Box>
  )
}
export { ChatFilterChip }
